<template>
  <div>
    <van-sticky>
      <van-nav-bar title="我的订单" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <!-- 主体内容 -->
    <div class="conWrap">
      <van-tabs
        v-model="active"
        @change="changeItem($event)"
        title-inactive-color="#9999"
        title-active-color="#DE4F53"
      >
        <van-tab title="待发货"></van-tab>
        <van-tab title="待收货"></van-tab>
        <van-tab title="已完成"></van-tab>
         <van-tab title="全部"></van-tab>

      </van-tabs>
      <div v-if="listData.length == 0" class="noData">
        暂无数据！
      </div>
      <div v-else>
        <div
          class="order-item-wrapper"
          @click="
            $router.push({ path: 'Order-detail', query: { id: item.id } })
          "
          v-for="item in listData"
        >
          <div class="order-header">
            <span>订单号:{{ item.order_num }}</span>
          </div>
          <div class="order-body" v-for="item in item.orderdetail">
            <div class="order-body-pic">
              <van-image :src="item.goods.imgs"></van-image>
            </div>
            <div class="order-body-info">
              <div class="order-body-info-name">
                {{ item.goods.goods_name }}
              </div>
              <div class="order-body-info-num">数量：x{{ item.nums }}</div>
            </div>
          </div>
          <div class="order-bottom">
            <div class="order-bottom-ctr">
              <div class="leftEl itemNum">
                共{{ item.orderdetail.length }}件商品
              </div>
              <div class="rightEl itemPrice">
                实付款:<span>￥</span>{{ item.cash }}<span></span>
              </div>
            </div>
          </div>
          <div class="itemBtn">
         
            <div class="rightEl">
              <van-button
                v-if="item.status == 1"
                plain
                round
                size="small"
                @click.stop="
                  $router.push({ path: 'logistics', query: { id: item.id } })
                "
                >查看物流</van-button
              >
              <van-button
                v-if="item.status == 1"
                plain
                round
                size="small"
                class="orange-color rightEl"
                @click.stop="confirmOrder(item.id)"
                >确认收货</van-button
              >
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Dialog } from "vant";
import { orderList, cancelOrders, sureOrder, payOrder } from "@/api/order";
export default {
  data() {
    return {
      active: 0,
      listData: [],
      status:0
    };
  },
  created() {

    this.active = parseInt(this.$route.query.type);
    if(this.$route.query.type){
    //   this.active=0;
      this.status=this.$route.query.type;
    }
    this.orderList();
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    changeItem(index) {
        this.status = index;
      this.orderList();
    },
    orderList() {
      orderList(this.status).then(res => {
        res.forEach(item => {
          item.orderdetail.forEach(itemSon => {
            itemSon.goods.imgs = itemSon.goods.imgs.split(",")[0];
          });
        });
        this.listData = res;
      });
    },
    // 取消订单
    cancelOrder(id) {
      Dialog.confirm({
        title: "取消订单",
        message: "确定取消此订单？"
      })
        .then(() => {
          cancelOrders(id).then(res => {
            this.$toast("取消订单成功");
            this.orderList();
          });
        })
        .catch(() => {
          this.$toast("取消操作");
        });
    },
    // 订单支付id
    // goOrderPay(id) {
    //   payOrder(id).then(res => {
    //     WeixinJSBridge.invoke(
    //       "getBrandWCPayRequest",
    //       {
    //         appId:res.appId,
    //         timeStamp: res.timeStamp,
    //         nonceStr: res.nonceStr,
    //         package: res.package,
    //         signType: res.signType,
    //         paySign: res.paySign
    //       },
    //       function(res) {
    //         if (res.err_msg == "get_brand_wcpay_request:ok") {
    //           this.$toast("支付成功");
    //           this.orderList();
    //         }
    //       }
    //     );
    //   });
    // },
    scanLog(id) {
      this.$router.push({ path: "logistics", query: { id: id } });
    },
    // 确认收货
    confirmOrder(id) {
      Dialog.confirm({
        title: "确认收货",
        message: "确认收货此订单？"
      })
        .then(() => {
          sureOrder(id).then(res => {
            this.$toast("确认收货成功!");
            this.orderList();
          });
        })
        .catch(() => {
          this.$toast("取消操作");
        });
    }
  }
};
</script>
<style lang="less" scoped>
.order-item-wrapper {
  width: 325px;
  margin: 15px auto;
  border-radius: 10px;
  box-shadow: 0 0 15px 2px #f5f5f5;
  background-color: #fff;
  padding: 0 15px;
  font-size: 13px;

  .order-header {
    height: 34px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f6f3f3;
  }

  .order-body {
    box-sizing: border-box;
    height: 120px;
    padding: 10px 0;
    border-bottom: 1px solid #f6f3f3;

    &-pic {
      width: 100px;
      height: 100px;
      float: left;
      margin-right: 15px;
      background-color: #f5f5f5;

      .van-image {
        width: 100%;
        height: 100%;
      }
    }

    &-info {
      float: left;
      width: 200px;
      height: 100px;

      &-name {
        height: 50px;
        line-height: 25px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
      }

      &-price {
        height: 25px;
        line-height: 25px;
        font-size: 12px;
        color: #ff6666;
      }

      &-num {
        line-height: 25px;
        font-size: 10px;
      }
    }
  }

  .order-bottom {
    height: 36px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-ctr {
      width: 100%;
      .itemNum {
        color: #333;
        font-size: 12px;
      }
      .itemPrice {
        span:last-child {
          color: #de4f53;
          font-size: 18px;
        }
      }
    }
  }
  .itemBtn {
    width: 100%;
    overflow: hidden;
    border-top: 1px solid #f6f3f3;
    padding: 10px;
    font-size: 16px;
    .van-button {
      margin-left: 11px;
    }
  }
  .orange-color {
    color: #de4f53;
    border-color: #de4f53;
  }
}
</style>
